<template>
    <top-bar />
    <main class="flex-shrink-0">
        <div class="container mb-5">
            <div class="row">
                <div class="col mb-2">
                    <h3>Upload</h3>
                </div>
            </div>
            <div class="row">
                <div class="col-md-6">
                    <div class="background-white" style="min-height: 500px;">
                        <form @submit="save">
                            <h5 class="mb-4">Informações Gerais</h5>
                            <div class="mb-3">
                                <label for="exampleInputPassword1" class="form-label">Upload PDF</label>
                                <div class="input-group custom-file-button">
                                    <i class="input-group-text fas fa-file-upload" for="inputGroupFile"></i>
                                    <input type="file" class="form-control" id="inputFile" name="Files" ref="file" @change="handleFileUpload" accept="application/pdf" required>
                                </div>
                            </div>
                            <div class="mb-3">
                                <label for="exampleInputPassword1" class="form-label">Número do Processo</label>
                                <input type="number" class="form-control" id="numberId" v-model="form.process" required>
                            </div>
                            <div class="mb-3">
                                <label class="form-label" for="descId">Descrição (Observação dos documentos)</label>
                                <textarea class="form-control" rows="5" id="descId" name="text" placeholder="Violação dos princípios administrativos..." v-model="form.description" required></textarea>
                            </div>
                            <button type="submit" class="btn btn-primary m-2" style="float:right">Criar</button>
                            <router-link class="btn btn-secondary m-2 btn-custom-cancel" to="/home" title="Cancelar" style="float:right">Cancelar</router-link>
                        </form>
                    </div>
                </div>
                <div class="col-md-6"></div>
            </div>
        </div>
    </main>
</template>

<script>
    import TopBar from '@/components/common/top-bar';
    import api from "@/services/api";

    export default {
        name: "FormIndex",
        data() {
            return {
                title: "Form Here",
                form: {
                    files: [],
                    process: '',
                    description: ''
                }
            }
        },
        components: {
            TopBar,
        },
        watch: {},
        methods: {
            handleFileUpload: function () {
                this.form.files = [];
                var error = false;
                for (var j = 0; j < this.$refs.file.files.length; j++) {
                    // File extension validation
                    if (/\.(pdf)$/i.test(this.$refs.file.files[j].name)) {
                        this.form.files.push({
                            file: this.$refs.file.files[j]
                        });
                    } else {
                        error = true;
                    }
                    if (j === this.$refs.file.files.length - 1) { // Trigger at the end of the loop
                        if (error) {
                            console.log("O arquivo não é .pdf");
                            this.$refs.file.value = null;
                        }
                    }
                }
            },
            save: function (e) {
                e.preventDefault();
                var formData = new FormData();
                formData.append("file", this.form.files[0].file);
                formData.append("process", this.form.process);
                formData.append("description", this.form.description);
                let self = this;
                api.post('/Inquiry', formData,
                    {
                        headers: { 'Content-Type': 'multipart/form-data' }
                    })
                    .then(function (response) { // Handle success
                        console.log(response);
                        self.cleanForm();
                    }).catch(function (e) { // Handle error
                        console.log(e);
                    }).finally(function () { // Always executed
                        console.log("Finished request.");
                    });
            },
            cleanForm: function () {
                this.form = {
                    files: [],
                    process: '',
                    description: ''
                };
                this.$refs.file.value = null;
            },
        },
        computed: {},
        created() { },
        mounted() { },
        unmounted() { },
    }
</script>

<style scoped>
    .input-group-text {
        padding: 0.6rem 0.75rem !important;
    }
    h3 {
        color: black;
        margin-top: 2%;
        text-align: left;
    }
    h5 {
        margin: 0% 0px auto 0px;
        color: black;
        text-align: left;
    }
    .background-white {
        background-color: white;
        padding: 25px 15px 0 !important;
    }
    .custom-file-button input[type=file] {
        margin-left: -2px !important;
    }
    .custom-file-button input[type=file]::-webkit-file-upload-button {
        display: none;
    }
    .custom-file-button input[type=file]::file-selector-button {
        display: none;
    }
    .custom-file-button:hover label {
        background-color: #dde0e3;
        cursor: pointer;
    }
    .fas {
        font-weight: 900 !important;
    }
    .btn-custom-cancel {
        background-color: #F0F7FF !important;
        color: #0073E6 !important;
        font-weight: inherit !important;
        padding: 8px 12px !important;
        border: 0 !important;
    }
</style>
