import { createRouter, createWebHistory } from 'vue-router';
import LoginIndex from '@/components/pages/login';
import LogoutIndex from '@/components/pages/logout';
import HomeIndex from '@/components/pages/home';
import FormIndex from '@/components/pages/form';
import NormalizeIndex from '@/components/pages/normalize';
import AnalyzerIndex from '@/components/pages/analyzer';

function authenticate(to, from, next) {
    var usuario = JSON.parse(window.localStorage.getItem('my_project'));
    if (usuario != null) {
        if (usuario.token != null) {
            next();
        }
    } else next({
        path: '/'
    })
}

const routes = [
    {
        path: '/',
        name: 'Login',
        component: LoginIndex,
    },
    {
        path: '/logout',
        name: 'Logout',
        component: LogoutIndex,
    },
    {
        path: '/home',
        name: 'Home',
        component: HomeIndex,
        beforeEnter: authenticate,
    },
    {
        path: '/form',
        name: 'Form',
        component: FormIndex,
        beforeEnter: authenticate,
    },
    {
        path: '/normalize',
        name: 'Normalize',
        component: NormalizeIndex,
        beforeEnter: authenticate,
    },
    {
        path: '/analyzer',
        name: 'Analyzer',
        component: AnalyzerIndex,
        beforeEnter: authenticate,
    },
];

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes,
});

export default router;
