<template>
    <top-bar />
    <main class="flex-shrink-0">
        <div class="container mb-5">
            <div class="row justify-content-md-center">
                <div class="col-md-auto">
                    <div class="div-center">
                        <div style="width: 500px;">
                            <div style="width: 70%;float: left;"> <h5 class="h5-custom"> Normalizando documento.. </h5> </div>
                            <div style="width: 30%;float: right;"> <h5>&nbsp;{{ progressValue }}% </h5> </div>
                        </div>
                        <span>
                            <progress id="file-progress-2" :value="progressValue" max="100" style="width: -webkit-fill-available;"></progress>
                        </span>
                    </div>
                </div>
            </div>
        </div>
    </main>
</template>

<script>
    import TopBar from '@/components/common/top-bar';

    export default {
        name: "NormalizeIndex",
        data() {
            return {
                title: "Normalize Index",
                progressValue: 0,
            }
        },
        components: {
            TopBar,
        },
        watch: {},
        methods: {
            move: function () {
                let self = this;
                let myInterval = setInterval(function () {
                    self.progressValue++;
                    if (self.progressValue === 100) {
                        clearInterval(myInterval);
                        self.redirectToAnalyzer();
                    }
                }, 50); // Interval = 1 second
            },
            redirectToAnalyzer: function () {
                window.onbeforeunload = null;
                let self = this;
                setTimeout(function () {
                    self.$router.push({ path: '/analyzer' });
                }, 500);
            },
        },
        computed: {},
        created() {
            this.move();
            window.onbeforeunload = function () { return true; };
        },
        mounted() { },
        unmounted() { },
    }
</script>

<style scoped>
    .div-center {
        position: fixed;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        width: 500px;
        /*background-color: red;*/
    }
    .h5-custom {
        text-align: right;
    }
    /* Progress css */
    progress::-moz-progress-bar {
        height: 6px;
        background: #0073E6;
        border-radius: 5px;
    }
    progress::-webkit-progress-value {
        height: 6px;
        background: #0073E6;
        border-radius: 5px;
    }
    progress::-webkit-progress-bar {
        height: 6px;
        background: #ADD6FF;
        border-radius: 5px;
    }
    progress {
        margin-top: 30px;
        height: 6px;
        background: #0073E6;
        border-radius: 5px;
        /*animation: progressBar 1.5s ease-in-out;*/
    }
    @keyframes progressBar {
        0% {
            left: 0;
            width: 0;
        }
        100% {
            left: 0;
            width: 100%;
        }
    }
</style>
