<template>
    <top-bar />
    <main class="flex-shrink-0">
        <div class="container mb-5">
            <div class="row">
                <div class="col">
                    <div class="mt-3 mb-4">
                        <label for="InputSearch" class="form-label">Busca inquérito</label>
                        <input type="text" class="form-control" id="InputSearch" placeholder="Ex: Nº Processo ou descrição" v-model="searchInput" @keydown.enter="getList">
                    </div>
                </div>
                <div class="col-auto content-center">
                    <div class="mt-5 mb-4">
                        <router-link class="btn btn-primary" to="/form">UPLOAD INQUÉRITO</router-link>
                    </div>
                </div>
            </div>
            <div class="row">
                <div class="table-responsive">
                    <table class="table" border="1">
                        <tbody>
                            <tr class="tr-head-1">
                                <td></td>
                                <td>Número Processo</td>
                                <td>Descrição</td>
                                <td style="text-align: center;">Data de inclusão</td>
                                <td style="text-align: center;">Status</td>
                                <td style="text-align: center;">Ação</td>
                            </tr>
                            <tr v-for="(item, index) in dataInquiry" :key="index">
                                <td style="text-align: center;">
                                    <a href="javascript:;" @click="confirmationDialog(index, item.process)">
                                        <i class="fas fa-trash" style="font-size: .75em;" title="Excluir"></i>
                                    </a>
                                </td>
                                <td> {{ item.process }} </td>
                                <td> {{ item.description }} </td>
                                <td style="text-align: center;color: blue;"> {{ item.inclusionDate }} </td>
                                <td style="text-align: center;">
                                    <span class="badge rounded-pill bg-primary" v-if="item.status==1">Não Analisado</span>
                                    <span class="badge rounded-pill bg-success" v-if="item.status==2">Analisado</span>
                                </td>
                                <td style="text-align: center;">
                                    <router-link class="btn btn-primary btn-sm" to="/normalize" title="ANALISAR" v-if="item.status==1"> ANALISAR </router-link>
                                    <router-link class="btn btn-success btn-sm" to="/analyzer" title="CONSULTAR" v-if="item.status==2"> CONSULTAR </router-link>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
            <div class="row">
                <div class="col"> Exibindo 1 a {{ dataInquiry.length }} do total de {{ dataInquiry.length }} </div>
                <div class="col-auto">
                    <nav aria-label="Page navigation example">
                        <ul class="pagination justify-content-center">
                            <li class="page-item disabled">
                                <a class="page-link" href="javascript:;" tabindex="-1" aria-disabled="true">
                                    <i class="fas fa-chevron-left"></i>
                                </a>
                            </li>
                            <li class="page-item active"><a class="page-link" href="javascript:;">1</a></li>
                            <li class="page-item"><a class="page-link" href="javascript:;">2</a></li>
                            <li class="page-item"><a class="page-link" href="javascript:;">3</a></li>
                            <li class="page-item">
                                <a class="page-link" href="javascript:;">
                                    <i class="fas fa-chevron-right"></i>
                                </a>
                            </li>
                        </ul>
                    </nav>
                </div>
            </div>
        </div>
    </main>
</template>

<script>
    import TopBar from '@/components/common/top-bar';
    import api from "@/services/api";

    export default {
        name: "HomeIndex",
        data() {
            return {
                title: "Home Index",
                searchInput: "",
                dataInquiry: [
                    { id: 1, process: "596 9 27749/2001", description: "Descrição 1", inclusionDate: "10/05/2023", status: 1 },
                    { id: 2, process: "596 9 27749/2002", description: "Descrição 2", inclusionDate: "10/05/2023", status: 1 },
                    { id: 3, process: "596 9 27749/2003", description: "Descrição 3", inclusionDate: "10/05/2023", status: 1 },
                    { id: 4, process: "596 9 27749/2004", description: "Descrição 4", inclusionDate: "10/05/2023", status: 2 },
                    { id: 5, process: "596 9 27749/2005", description: "Descrição 5", inclusionDate: "10/05/2023", status: 1 },
                    { id: 6, process: "596 9 27749/2006", description: "Descrição 6", inclusionDate: "10/05/2023", status: 2 },
                    { id: 7, process: "596 9 27749/2007", description: "Descrição 7", inclusionDate: "10/05/2023", status: 1 },
                    { id: 8, process: "596 9 27749/2008", description: "Descrição 8", inclusionDate: "10/05/2023", status: 1 },
                    { id: 9, process: "596 9 27749/2009", description: "Descrição 9", inclusionDate: "10/05/2023", status: 1 },
                    { id: 10, process: "596 9 27749/2010", description: "Descrição 10", inclusionDate: "10/05/2023", status: 1 },
                ],
            }
        },
        components: {
            TopBar,
        },
        watch: {},
        methods: {
            getList: function () {
                console.log(this.searchInput);
                var paramsReq = {
                    search: this.searchInput.trim(),
                    page: 1,
                }
                api.get('/Inquiry', { params: paramsReq })
                    .then(function (response) { // Handle success
                        console.log(response);
                    }).catch(function (e) { // Handle error
                        console.log(e);
                    }).finally(function () { // Always executed
                        console.log("Finished request.");
                    });
            },
            confirmationDialog: function (idx, process) {
                if (confirm("Realmente deseja exluir o inquérito de Nº " + process +" ?")) {
                    this.dataInquiry.splice(idx, 1);
                } else {
                    console.log("Operação cancelada!");
                }
            },
        },
        computed: {},
        created() {},
        mounted() {},
        unmounted() {},
    }
</script>

<style scoped>
    .content-center {
        align-items: center;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
    }
    .tr-head-1 {
        background-color: #F3F4F6 !important;
        color: #6F7582 !important;
        font-weight: 500 !important;
    }
    tbody {
        background-color: #fff !important;
    }
    .bg-primary {
        background-color: #F0F7FF !important;
        color: #0073E6 !important;
        font-weight: inherit !important;
        padding: 8px 12px !important;
    }
    .bg-success {
        background-color: #EDFEF2 !important;
        color: #0EAA42 !important;
        font-weight: inherit !important;
        padding: 8px 12px !important;
    }
    .btn-success {
        background-color: #0EAA42 !important;
        border-color: #0EAA42 !important;
    }
</style>
