import axios from 'axios';
import qs from 'qs';

const api = axios.create();

//let baseUrlApi = JSON.parse(window.localStorage.getItem('dataAccess')).baseUrlAPI;
let baseUrlApi = "https://localhost:7045";
baseUrlApi = baseUrlApi.replace(/\/$/, '') + '/api';
api.defaults.baseURL = baseUrlApi;

api.defaults.paramsSerializer = {
    serialize: (params) => {
        return qs.stringify(params, { arrayFormat: 'indices', allowDots: true });
    },
};

api.defaults.headers.post['Content-Type'] = 'application/json;charset=utf-8';
api.defaults.headers.post['Access-Control-Allow-Origin'] = '*';
api.defaults.headers.get['Content-Type'] = 'application/json;charset=utf-8';
api.defaults.headers.get['Access-Control-Allow-Origin'] = '*';

api.defaults.headers.common['X-Time-Zone'] = window.Intl.DateTimeFormat().resolvedOptions().timeZone;

api.interceptors.request.use(
    config => {
        //config.headers['Authorization'] = `Bearer ${JSON.parse(window.localStorage.getItem('dataAccess')).accessToken}`;
        config.headers['Authorization'] = `Bearer hfsef2z84zgr88b4b28jh4c2b4yjcv2x84er8e2cer48ry48vs68tr4y1s8v6as8e4y862`;
        return config;
    }, error => {
        return Promise.reject(error);
    }
);

export default api;
